<template>
	<div>
		<div id="player-container">
			<div class="watermark"></div>
		</div>
		<button @click="playVideo">{{ jianting }}</button>
	</div>
</template>
<script>
import { ref, onMounted, watch } from 'vue'
export default {
	name: 'MyVideoPlayer',
	props: ['medium'],

	data() {
		return {
			player: null,
			videoWidth: 860,
			jianting: '',
		}
	},

	methods: {
		playVideo() {
			if (this.player) {
				// this.player.play()
				console.log(this.player)
				this.jianting = 'gaibian'
			}
		},
	},
	mounted() {
		// 
		//player.alicdn.com/video/editor.mp4
		console.log(this.medium)
		this.player = new window.Aliplayer({
			id: 'player-container',
			source: this.medium,
			autoplay: true,
			width: '100%',
			height: `${this.videoHeight}px`,
			isLive: false,
			autoPlayDelay: 3,
			autoPlayDelayDisplayText: '...',
			// controlBarVisibility: 'never', // 
			// useH5Prism: false, // H5
		})
		// 
		this.player.on('ended', this.handleVideoEnded)
	},
	computed: {
		videoHeight() {
			const videoAspectRatio = 16 / 9 //  16:9
			return this.videoWidth / videoAspectRatio
		},
	},
	watch: {
		jianting(newVal, oldVal) {
			//  jianting 
			console.log('jianting :', newVal, oldVal)
		},
	},
}
</script>
<style>
#player-container {
	position: relative;
}

.watermark {
	position: absolute;
	top: 10px;
	right: 10px;
	width: 100px;
	height: 50px;
	opacity: 0.7;
	/*  */
	background: url('./../assets/logo.svg'); /*  */
	/*  */
}
</style>
