<template>
	<div v-my-directive :data-title="article.title" :data-description="article.description" :data-keywords="article.keywords" class="main">
		<div>
			<div class="content">
				<!-- PHP -->
				<div>
					<el-breadcrumb separator-class="el-icon-arrow-right">
						<el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
						<el-breadcrumb-item v-for="(item, index) in comn" :key="index" :to="{ path: '/column/' + item.alias + '.html' }">{{ item.name }}</el-breadcrumb-item>
					</el-breadcrumb>
				</div>
				<!--  -->

				<div class="ThinLineLayout">
					<!--  -->
					<Aliplayer v-if="article.medium" :medium="article.medium"></Aliplayer>
					<div class="Genericpadding content">
						<div class="bold">{{ article.title }}</div>
						<div class="articlebt minfont grey">
							<div class="NavigationRight">
								<div>{{ ': ' + article.username }}</div>
								<div>{{ ': ' + article.pidname }}</div>
								<div>{{ ': ' + article.Creationtime }}</div>
							</div>
							<div class="NavigationRight">
								<!--  -->
								<div>:{{ collect }}</div>
								<div>:{{ Like }}</div>
								<div>:{{ article.PageView }}</div>
							</div>
						</div>
					</div>
					<!--  -->
					<!--  -->
					<!--  -->
					<div class="Genericpadding interval">
						<div class="abstract">
							<div>
								<div class="iconfont icon-gaojian-zhaiyao Green"></div>
								<p>{{ article.description }}</p>
							</div>
						</div>

						<div class="pre TopRectangles" :class="{ PurchaseStyle: purchase }">
							<readOnly :contentid="contentid" post="getarticle"></readOnly>
							<div v-if="purchase" class="Positioningmask">
								<el-button type="danger" @click="purchase = !purchase"></el-button>
							</div>
						</div>
						<div class="center NavigationRight">
							<div class="elliptic" @click="Favoritelikes(2)">
								<div class="center NavigationRight">
									<div class="iconfont icon-shoucang centerend"></div>
									<div v-if="usercollect" class="center minfont"></div>
									<div v-else class="center minfont"></div>
									<div class="center minfont">{{ collect }}</div>
								</div>
							</div>
							<div class="grantareward">
								<p class="center"></p>
							</div>
							<div class="elliptic" @click="Favoritelikes(1)">
								<div class="center NavigationRight">
									<div class="iconfont icon-shoucang1 centerend"></div>
									<div v-if="userlike" class="center minfont"></div>
									<div v-else class="center minfont"></div>
									<div class="center minfont">{{ Like }}</div>
								</div>
							</div>
						</div>
						<div class="abstract">
							<div>
								<p></p>
								<p> :{{ domain }}</p>
							</div>
						</div>
					</div>
					<!--  -->
					<div class="Genericpadding share grey">
						<div></div>
						<div>1</div>
						<div>2</div>
					</div>
				</div>
				<!--   -->
				<div class="Horizontalspacing interval">
					<div class="Genericpadding Intervalvertical">
						<a v-if="articlelist && articlelist[page - 1]" :href="'/article/' + articlelist[page - 1].alias + '/' + articlelist[page - 1].id + '.html'"></a>
						<div v-else></div>
						<a v-if="articlelist && articlelist[page - 1]" :href="'/article/' + articlelist[page - 1].alias + '/' + articlelist[page - 1].id + '.html'">{{ articlelist[page - 1].title }}</a>
						<div v-else></div>
					</div>
					<div class="Genericpadding">
						<a v-if="articlelist && articlelist[page / 1 + 1]" :href="'/article/' + articlelist[page / 1 + 1].alias + '/' + articlelist[page / 1 + 1].id + '.html'"></a>
						<div v-else></div>
						<a v-if="articlelist && articlelist[page / 1 + 1]" :href="'/article/' + articlelist[page / 1 + 1].alias + '/' + articlelist[page / 1 + 1].id + '.html'">{{ articlelist[page / 1 + 1].title }}</a>
						<div v-else></div>
					</div>
				</div>
				<div class="ThinLineLayout">
					<div class="Genericpadding"></div>
					<a class="newlist" v-for="(item, index) in newlist" :key="index" :href="'/article/' + item.alias + '/' + item.id + '.html'"
						><div style="white-space: nowrap; text-overflow: ellipsis; overflow: hidden; max-width: 620px" class="turngreen">{{ item.title }}</div> <p class="grey">{{ item.Creationtime }}</p></a
					>
				</div>
				<div class="ThinLineLayout">
					<div class="Genericpadding articlebt">
						<div></div>
						<div class="minfont grey">5</div>
					</div>
					<div v-if="user" class="Genericpadding Horizontalspacingg interval">
						<img src="./../assets/img/avatar.png" alt="" class="pimg" />
						<div class="Intervalvertical">
							<dexedi height="100px" ref="child"></dexedi>
							<div class="buttons" @click="processReply"></div>
						</div>
					</div>
					<div v-if="Replylist[0]" class="Genericpadding Intervalvertical">
						<!--  -->
						<div v-for="(item, index) in Replylist" :key="index" class="Intervalvertical">
							<div class="articlebt minfont">
								<div class="Horizontalspacing minxgap"
									><img src="./../assets/img/avatar.png" alt="" class="pimgmin" />
									<p class="center">{{ item.username }}</p>
								</div>
								<div class="Horizontalspacing minxgap grey">
									<p>{{ item.Creationtime }}</p>
									<div></div>
								</div>
							</div>
							<div class="abstracts pmaring">
								<Reply :content="item.content"> </Reply>
							</div>
							<!--  -->
							<ul v-if="item.child" class="pmaring Intervalvertical">
								<div v-for="(item, index) in item.child" :key="index" class="Intervalvertical">
									<div class="articlebt minfont">
										<div class="Horizontalspacing minxgap"
											><img src="./../assets/img/avatar.png" alt="" class="pimgmins" />
											<p class="center">{{ item.username }}</p>
										</div>
										<div class="Horizontalspacing minxgap grey">
											<p class="center">{{ item.Creationtime }}</p>
											<div></div>
										</div>
									</div>
									<div class="abstracts pmaring">
										<Reply :content="item.content"> </Reply>
									</div>
								</div>

								<!--      -->
							</ul>
						</div>
					</div>
					<div v-if="!Replylist[0]" class="Genericpadding">
						<div class="abstracts">
							<p class="center">~</p>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div> <left></left> </div>
	</div>
</template>
<script>
import { mapState } from 'vuex'
import { ref } from 'vue'
import left from './../components/left'
import dexedi from './../components/dexedi'
import { watch } from 'vue'
import readOnly from './../components/readOnly'
import Reply from './../components/Reply'
import Aliplayer from './../components/Aliplayer'
import { dataRequest as api, setText } from '@/api/api'

import 'highlight.js/styles/androidstudio.css'
export default {
	name: 'name',
	computed: {
		...mapState(['logon', 'user']),
	},
	components: {
		// 
		left,
		readOnly,
		Aliplayer,
		dexedi,
		Reply,
	},
	data() {
		return {
			contentid: this.$route.params.articid.replace(/.html/g, ''),
			loading: false,
			article: '',
			// 
			domain: window.location.href,
			comn: [],
			PurchaseStyle: 'PurchaseStyle',
			purchase: false,
			articlelist: [],
			newlist: [],
			// 
			Like: 0,
			userlike: false,
			// 
			collect: 0,
			usercollect: false,
			CommentID: '',
			Replylist: [],
			page: '',
		}
	},
	methods: {
		// 
		async processReply() {
			let value = this.$refs.child.delta()
			let delta = value[0]
			if (setText(value[1])) {
				// 
				console.log(delta.ops)
				let arr = { PostID: this.article.id, userid: this.user.id, CommentID: this.CommentID, content: delta.ops }
				arr = await api(['post=PostReply', arr])
			} else {
				// 
				this.$message.error('')
			}
			this.CommentID = ''
		},
		//  
		async getprocessReply() {
			let arr = await api(['post=getPostReply', { PostID: this.article.id }])
			if (arr.code == 200) {
				this.Replylist = arr.data
			}
		},
		// 
		async Receive_data(value) {
			this.article = value
			this.article.Price = Math.round(this.article.Price * 1000) / 1000
			// 
			this.Like = this.article.Like.length
			// 
			this.collect = this.article.collect.length
			if (this.article.Price > 0) {
				this.purchase = true
			}
			let arr = await api(['post=acbrowsing', [this.$route.params.articid.replace(/.html/g, '')]])
			if (this.user) {
				this.getFavoritelikes()
			}
		},
		// HTML 
		// 
		async get() {
			let arr = await api(['post=Obtainclassification', this.$route.params.comn])
			if (arr.code == 200) {
				this.comn = arr.data
			}
			let alice = this.$route.params.comn
			let array = await api(['post=getarticle', { alice }])

			if (array.code == 200) {
				this.articlelist = array.data
				console.log(this.articlelist)
				for (let i = 0; i < this.articlelist.length; i++) {
					console.log(this.articlelist[i].id, this.contentid)
					if (this.articlelist[i].id == this.contentid) {
						this.page = i
					}
				}
				console.log('', this.page)
				this.newlist = array.data.slice(-10)
				this.newlist.reverse()
			}
			this.getprocessReply()
		},
		//  
		async Favoritelikes(ele) {
			if (this.user) {
				let arr = await api(['post=LikeCollection', { type: ele, PostID: this.article.id, userid: this.user.id }])
				if (ele == 1) {
					this.userlike = !this.userlike
					this.Like = this.userlike ? this.Like / 1 + 1 : this.Like / 1 - 1
				} else {
					this.usercollect = !this.usercollect
					this.collect = this.usercollect ? this.collect / 1 + 1 : this.collect / 1 - 1
				}
			} else {
				// 
				this.$message.error('')
			}
		},
		// 
		async getFavoritelikes() {
			// 
			// 
			this.article.Like.forEach(element => {
				console.log(element.id, this.user.id)
				if (element.userid == this.user.id) {
					this.userlike = true
				}
			})
			this.article.collect.forEach(element => {
				console.log(element.id, this.user.id)
				if (element.userid == this.user.id) {
					this.usercollect = true
				}
			})
		},
	},
	mounted() {
		// :
		this.get(), window.scrollTo({ behavior: 'instant', top: 0 })
	},
	updated() {
		this.loading = false
	},
}
</script>
<style scoped>
.pre,
.pre pre,
.pre span {
	cursor: default;
	-moz-user-select: text;
	-webkit-user-select: text;
	-ms-user-select: text;
	-khtml-user-select: text;
	user-select: text;
	font-size: 13px;
	overflow: hidden;
	/*  */
}
.PurchaseStyle {
	height: 200px;
	overflow: hidden;
}
.Positioningmask {
	width: 100%;
	height: 200px;
	position: absolute;
	top: 0px;
	background: linear-gradient(to bottom, #ffffff8b, #000000);
}
.Positioningmask :deep(.el-button) {
	width: 300px;
	height: 40px;
	font-size: 20px;
	font-weight: bold;
	position: absolute;
	right: 0;
	left: 0;
	margin: auto;
	bottom: 35px;
}

.newlist {
	display: flex;
	flex-direction: row;
	place-content: space-between;
	gap: 20px;
	width: 100%;
	padding: 10px 20px;
	background: #fff;
}
</style>
